.booklist{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
    margin-bottom: 20px;
}

.book {
    width: 12em;
    height: 14em;
    background-color: #ffffff;
    overflow: hidden;
    margin: 1em 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
}

.title{
    margin: 0.5em;
    text-align: center;
}

.author{
    font-size: 0.7em;
    text-align: center;
}

button{
    border: none;
    outline: none;
}

a{
    text-decoration: none;
}

a:hover, button:hover{
    cursor: pointer;

}

.book > a{
    padding: 1em 0.2em;
    text-decoration: none;
    text-align: center;
    background-color: #AD002E;
    color: white;
    font-size: 1em;
    margin-top: auto;
}


@media all and (min-width: 680px){

    .book{
        margin: 1em 2em;
    }

    .book > a{
        padding: 1em 1em;
    }

}
